@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.small-font {
  font-size: 13px;
  line-height: 24px;
}
.bold-middle-smaller-font {
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
}
.bold-middle-small-font {
  font-weight: 700;
  font-size: 17px;
  line-height: 18px;
}
.bold-middle-middle-font {
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
}
.bold-middle-big-font {
