}
.clear-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page__body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  margin: 0;
}
.button {
  width: 100%;
  border: 2px solid #444444;
  color: #444444;
  margin: 0;
  padding: 16px 0 16px 0;
  text-align: center;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #fff;
}
.button:hover {
  background-color: #444444;
  color: #fff;
}
.button:active {
  background-color: #444444;
  color: rgba(255, 255, 255, 0.3);
}
.copyright__text {
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
  margin: 0;
}
.copyright__link {
  text-decoration: none;
  color: #63d1bb;
  margin-left: 27px;
  padding-top: 5px;
}
.choice {
  display: flex;
  align-items: center;
  margin-bottom: 23px;
}
.choice input:checked + .choice__mark::after {
  opacity: 1;
}
.choice:hover .choice__mark {
  border-color: #62d1ba;
}
.choice:active .choice__mark {
  border-color: rgba(0, 0, 0, 0.3);
}
.choice__mark {
  position: relative;
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid #444444;
  margin-right: 18px;
}
.choice__mark::after {
  position: absolute;
  right: 50%;
  bottom: 50%;
  background-color: #62d1ba;
  transform: translate(50%, 50%);
  border-radius: 50%;
  width: 16px;
  height: 16px;
  content: "";
  opacity: 0;
}
.choice__mark--checkbox {
  border-radius: 0%;
  margin-right: 23px;
}
.choice__mark--checkbox::after {
  background-image: url("../img/checkmark.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;
  width: 100px;
  height: 100px;
  border-radius: 0%;
}
.text-field {
  display: flex;
  flex-direction: column;
  position: relative;
}
.text-field:hover .text-field__input,
.text-field:focus .text-field__input,
.text-field:active .text-field__input {
  border-color: #62d1ba;
}
.text-field:hover .text-field__icon,
.text-field:focus .text-field__icon,
.text-field:active .text-field__icon {
  color: #62d1ba;
  opacity: 1;
}
.text-field__label {
  font-weight: 700;
  font-size: 17px;
  line-height: 18px;
}
.text-field__input {
  font-family: inherit;
  font-size: 17px;
  line-height: 18px;
  border: 0;
  outline: none;
  border-bottom: 2px solid #000;
  padding: 17px 0 17px 0;
}
.text-field__input--additional-info {
  border: 2px solid #000;
  margin: 22px 0 17px 0;
  padding-left: 17px;
  width: 100%;
  height: 142px;
}
.text-field__icon {
  position: absolute;
  right: 7px;
  bottom: 17px;
  opacity: 0.2;
}
.social__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: center;
  padding: 28px 30px 26px 30px;
